.product-description{padding:40px 0;display:flex;flex-direction:column;gap:20px;max-width:1200px;margin:0 auto;background-color:#fff}.description-title{font-size:2.4rem;font-weight:600;text-align:center;margin-bottom:20px}.product-content{display:flex;flex-direction:column;gap:20px}.product-features{display:flex;justify-content:flex-start;gap:40px;margin-bottom:10px}.product-features .feature-item{display:flex;flex-direction:column;align-items:center;gap:5px}.product-features .feature-item img{width:32px;height:32px;-o-object-fit:contain;object-fit:contain;border:1px solid #3a62d0;border-radius:5px;padding:5px}.product-features .feature-item span{font-size:1.4rem;font-weight:500;color:#333}@media(max-width: 576px){.product-features{justify-content:center;gap:20px}}.product-main-content{display:grid;grid-template-columns:1fr 1fr;gap:30px}@media(max-width: 768px){.product-main-content{grid-template-columns:1fr}}.product-main-content .specs-column{display:flex;flex-direction:column;gap:15px;padding-right:20px}.product-main-content .main-image{width:100%;height:100%;display:flex;align-items:center;justify-content:center;background-color:#f5f5f5;border-radius:8px;overflow:hidden}.product-main-content .main-image img{width:100%;height:100%;-o-object-fit:contain;object-fit:contain}.spec-group,.spec-item{display:flex;gap:10px}@media(max-width: 576px){.spec-group,.spec-item{flex-direction:column;gap:5px}}.spec-label{font-size:1.4rem;font-weight:600;color:#333;width:120px;flex-shrink:0;text-transform:uppercase}.spec-value{font-size:1.4rem;color:#555}.spec-value p{margin:0;line-height:1.6}.spec-scale{flex:1}.spec-scale .scale-bar{position:relative;width:100%;height:6px;background-color:#eaeaea;border-radius:3px;margin-bottom:8px}.spec-scale .scale-bar .scale-fill{position:absolute;left:0;height:100%;width:50%;border-radius:3px;background-color:#000}.spec-scale .scale-labels{display:flex;justify-content:space-between}.spec-scale .scale-labels span{font-size:1.2rem;color:#666}.made-in{margin-top:10px;font-size:1.4rem;color:#333}.product-features-grid{display:grid;grid-template-columns:repeat(4, 1fr);gap:15px;margin-top:20px}@media(max-width: 992px){.product-features-grid{grid-template-columns:repeat(2, 1fr)}}@media(max-width: 576px){.product-features-grid{grid-template-columns:1fr}}.product-features-grid .feature-card{border:1px solid #eee;border-radius:4px;overflow:hidden;display:flex;flex-direction:column}.product-features-grid .feature-card img{width:100%;aspect-ratio:1/1;-o-object-fit:cover;object-fit:cover}.product-features-grid .feature-card h3{font-size:1.5rem;font-weight:600;margin:10px 10px 5px;color:#333}.product-features-grid .feature-card p{font-size:1.3rem;color:#666;margin:0 10px 10px;line-height:1.5}